//
// --------------------------------
//  - 

// /  
%hidden {
	display: none !important;
	visibility: hidden; }

//   
%a11y-hidden {
	overflow: hidden;
	position: absolute;
	clip: rect(0 0 0 0);
	clip: rect(0, 0, 0, 0);
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;

	//    +  
	&%focusable:active,
	&%focusable:focus {
		overflow: visible;
		position: static;
		clip: auto;
		width: auto;
		height: auto;
		margin: 0; } }

//   ,   
%invisible {
	visibility: hidden; }

//       
%blind {
	visibility: hidden;
	overflow: hidden;
	position: absolute;
	top: 0;
	left: -999em;
	width: 0;
	height: 0;
	font-size: 0;
	line-height: 0;
	text-align: left; }
//    
%blind-pos {
	position: relative;
	visibility: hidden;
	overflow: hidden;
	width: 0;
	height: 0;
	font-size: 0;
	line-height: 0; }
//
// --------------------------------
//    
// 1. block formatting context
%hidden-block-1 {
	overflow: hidden;
	border: 0;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%); }
%hidden-block-2 {
	overflow: hidden;
	border: 0;
	position: absolute;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%); }
// 2. inline fomatting context
%hidden-inline {
	overflow: hidden;
	display: inline-block;
	border: 0;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%); }
